.clients
    background-image: linear-gradient(120deg, #ffdbea 0%, #ebedee 100%)

    &__container

    &__title
        text-align: center
        margin-bottom: 60px

    &__items
        display: grid
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr))
        gap: 50px

    &__item
        display: flex
        align-items: center
        justify-content: center

    &__link
        display: block
        img
            width: 100%
