* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #fc2db7;
  outline-offset: 4px;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Montserrat", sans-serif;
  color: #000;
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Montserrat", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  outline: none;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__home {
  padding: 200px 0 30px 0;
  min-height: 600px;
}
.main__benefits {
  padding: 70px 0;
}
.main__popular {
  padding: 70px 0 50px 0;
}
.main__partners {
  padding: 70px 0;
}
.main__new {
  padding: 70px 0 50px 0;
}
.main__categories {
  padding: 70px 0;
}
.main__clients {
  padding: 70px 0;
}

.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-color: #654f5e;
  font-weight: 500;
  border-radius: 20px;
  transition: box-shadow 0.3s ease, background-color 0.3s ease;
}
@media (any-hover: hover) {
  .btn:hover {
    box-shadow: 0 0 3px 3px rgba(194, 63, 119, 0.4);
    background-color: #fc2db7;
  }
}

.title {
  position: relative;
  font-weight: 700;
  font-size: 42px;
  color: #654f5e;
}
.title span {
  color: #fc2db7;
}
.title::after {
  content: "";
  position: absolute;
  width: 15%;
  height: 10px;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fc2db7;
}

.product-item {
  width: 180px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 10px 5px rgba(194, 63, 119, 0.4);
}
.product-item__image {
  width: 100%;
  margin-bottom: 10px;
}
.product-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.product-item__content {
  padding: 10px;
  color: #654f5e;
}
.product-item__title {
  display: block;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #654f5e;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .product-item__title:hover {
    color: #fc2db7;
  }
}
.product-item__prices {
  margin-bottom: 10px;
}
.product-item__price {
  margin-right: 10px;
  font-weight: 700;
}
.product-item__price_old {
  font-weight: 500;
  text-decoration: line-through;
}
.product-item__bottom {
  display: grid;
  grid-template-columns: 1.5fr 1.5fr 3fr;
  gap: 10px;
  width: 100%;
}

.item-bottom__label {
  display: block;
  width: 100%;
  border: 1px solid #654f5e;
}
.item-bottom__input {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
}
.item-bottom__btns {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.item-bottom__btn {
  border-radius: 10px;
}
.item-bottom__btn_plus {
  margin-bottom: 5px;
}
.item-bottom__button {
  border-radius: 10px;
  padding: 5px;
}

@font-face {
  font-family: "icons";
  src: url("../resources/icons/icons.ttf") format("truetype"), url("../resources/icons/icons.woff") format("woff"), url("../resources/icons/icons.woff2") format("woff2"), url("../resources/icons/icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=icon-]:before,
[class*=" icon-"]:before {
  font-family: "icons";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^=icon-]:before,
[class*=" icon-"]:before {
  display: block;
}

.icon-cart:before {
  content: "\e902";
}

.icon-register:before {
  content: "\e90e";
}

.icon-user:before {
  content: "\e913";
}

.icon-paper-plane:before {
  content: "\e910";
}

.icon-world:before {
  content: "\e911";
}

.icon-cupcake:before {
  content: "\e912";
}

.icon-favorite:before {
  content: "\e908";
}

.icon-calendar:before {
  content: "\e900";
}

.icon-callback:before {
  content: "\e901";
}

.icon-catalog:before {
  content: "\e903";
}

.icon-chevron-left:before {
  content: "\e904";
}

.icon-chevron-right:before {
  content: "\e905";
}

.icon-close:before {
  content: "\e906";
}

.icon-facebook:before {
  content: "\e907";
}

.icon-instagram:before {
  content: "\e909";
}

.icon-location:before {
  content: "\e90a";
}

.icon-phone:before {
  content: "\e90b";
}

.icon-search:before {
  content: "\e90c";
}

.icon-telegram:before {
  content: "\e90d";
}

.icon-vk:before {
  content: "\e90f";
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-light.woff2") format("woff2"), url("../Montserrat-light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Black.woff2") format("woff2"), url("../Montserrat-Black.woff") format("woff");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.header.active {
  background-color: rgba(238, 238, 238, 0.95);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
}
.header.active .search-header__body {
  box-shadow: 0 0 2px 2px rgba(101, 79, 94, 0.4);
}
.header__body {
  min-height: 150px;
  display: flex;
  align-items: center;
}
.header__logo {
  display: block;
  flex: 0 0 160px;
  background-color: #fc2db7;
  border-radius: 10px;
  border: 7px solid #ffffff;
  margin-right: 40px;
}
.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header__content {
  flex: 1 1 auto;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}
.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
}
.header__bottom {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-header__phones {
  display: flex;
  align-items: center;
}
.top-header__phone {
  display: flex;
  align-items: center;
  color: #654f5e;
  font-size: 14px;
  font-weight: 700;
  transition: color 0.3s ease;
}
.top-header__phone:not(:last-child) {
  margin-right: 25px;
}
.top-header__phone:before {
  font-size: 16px;
  margin-right: 5px;
}
@media (any-hover: hover) {
  .top-header__phone:hover {
    color: #fc2db7;
  }
}

.search-header__body {
  display: flex;
  align-items: center;
  height: 40px;
  width: 400px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 2px;
  transition: box-shadow 0.3s ease;
}
.search-header__body:focus-within {
  box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.5);
}
.search-header__label {
  padding: 2px 10px;
  width: 100%;
  height: 100%;
}
.search-header__input {
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #654f5e;
  outline: none;
  padding-left: 5px;
  transition: box-shadow 0.3s ease;
}
.search-header__btn {
  flex: 0 0 36px;
  height: 36px;
  padding: 5px;
  font-size: 16px;
  border-radius: 100%;
  background-color: #654f5e;
}

.bottom-header__list {
  display: flex;
  align-items: center;
}
.bottom-header__item:not(:last-child) {
  margin-right: 30px;
}
.bottom-header__link {
  color: #654f5e;
  position: relative;
  font-weight: 700;
}
.bottom-header__link::before, .bottom-header__link::after {
  content: "";
  position: absolute;
  bottom: -5px;
  width: 0;
  height: 2px;
  opacity: 0;
  background-color: #fc2db7;
  transition: all 0.3s ease 0s;
}
.bottom-header__link::before {
  left: 50%;
}
.bottom-header__link::after {
  right: 50%;
}
@media (any-hover: hover) {
  .bottom-header__link:hover {
    color: #fc2db7;
  }
  .bottom-header__link:hover::before, .bottom-header__link:hover::after {
    width: 50%;
    opacity: 1;
  }
}
.bottom-header__actions {
  display: flex;
  align-items: center;
}
.bottom-header__login, .bottom-header__registration, .bottom-header__cart {
  font-size: 14px;
  margin-right: 20px;
  font-weight: 700;
  color: #654f5e;
  transition: color 0.3s ease;
}
.bottom-header__login:before, .bottom-header__registration:before, .bottom-header__cart:before {
  margin-bottom: 5px;
  font-size: 22px;
}
.bottom-header__login span, .bottom-header__registration span, .bottom-header__cart span {
  display: inline-block;
  font-weight: 700;
}
@media (any-hover: hover) {
  .bottom-header__login:hover, .bottom-header__registration:hover, .bottom-header__cart:hover {
    color: #fc2db7;
  }
}
.bottom-header__burger {
  display: none;
}
.bottom-header__callback {
  height: 40px;
  padding: 5px 15px;
  font-size: 14px;
  background-color: #fc2db7;
}
.bottom-header__callback::before {
  margin-right: 5px;
  font-size: 26px;
}

.home {
  background-image: linear-gradient(120deg, #ebedee 0%, #ffdbea 100%);
}
.home__slider {
  position: relative;
  padding: 0 40px;
}

.slider-home__body {
  height: 400px;
  width: 100%;
}
.slider-home__slide {
  width: 100%;
  height: 100%;
  position: relative;
}
.slider-home__arrow {
  position: absolute;
  top: 40%;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  z-index: 10;
  background-color: #654f5e;
}
.slider-home__arrow_prev {
  left: -10px;
}
.slider-home__arrow_next {
  right: -10px;
}
.slider-home__dotts {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.slide-home__image {
  width: 100%;
  height: 100%;
}
.slide-home__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.slide-home__content {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 45%;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
}
.slide-home__title {
  color: #654f5e;
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.slide-home__descr {
  color: #654f5e;
  font-weight: 500;
  margin-bottom: 20px;
}
.slide-home__btn {
  height: 40px;
  padding: 5px 15px;
}

.benefits__title {
  text-align: center;
  margin-bottom: 60px;
}
.benefits__body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 40px;
}
@media (max-width: 768px) {
  .benefits__body {
    gap: 20px;
  }
}
.benefits__item {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px 40px;
}

.item-benefits__icon {
  flex: 0 0 100px;
  width: 100px;
  margin-bottom: 20px;
}
.item-benefits__icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.item-benefits__title {
  color: #654f5e;
  min-height: 45px;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 15px;
}
@media (max-width: 480px) {
  .item-benefits__title {
    min-height: 0;
  }
}
.item-benefits__descr {
  color: #654f5e;
  line-height: 120%;
}

.popular {
  background-image: linear-gradient(120deg, #ffdbea 0%, #ebedee 100%);
}
.popular__title {
  text-align: center;
  margin-bottom: 60px;
}
.popular__slider {
  position: relative;
  padding: 15px 18px 15px 12px;
}
.popular__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 100%;
  z-index: 10;
}
.popular__arrow_prev {
  left: 0;
}
.popular__arrow_next {
  right: 0;
}
.popular__view-all {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.popular__btn {
  height: 40px;
  padding: 5px 25px;
}

.partners__title {
  text-align: center;
  margin-bottom: 80px;
}
.partners__slider {
  min-height: 106px;
}
.partners__slide {
  min-height: 106px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.partners__link {
  height: 100%;
  display: block;
}

.new {
  background-image: linear-gradient(120deg, #ebedee 0%, #ffdbea 100%);
}
.new__title {
  text-align: center;
  margin-bottom: 60px;
}
.new__slider {
  position: relative;
  padding: 15px 18px 15px 12px;
}
.new__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 100%;
  z-index: 10;
  background-color: #654f5e;
}
.new__arrow_prev {
  left: 0;
}
.new__arrow_next {
  right: 0;
}
.new__view-all {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.new__btn {
  height: 40px;
  padding: 5px 25px;
}

.categories__title {
  text-align: center;
  margin-bottom: 60px;
}
.categories__body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}
.categories__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: 2px solid #654f5e;
  border-radius: 10px;
  padding: 20px;
}

.item-categories__image {
  width: 100%;
  padding: 30px 60px;
}
.item-categories__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.item-categories__title {
  flex: 1 1 auto;
  font-size: 22px;
  font-weight: 700;
  color: #654f5e;
  margin-bottom: 20px;
}
.item-categories__btn {
  height: 45px;
  width: 100%;
  padding: 5px 15px;
}

.clients {
  background-image: linear-gradient(120deg, #ffdbea 0%, #ebedee 100%);
}
.clients__title {
  text-align: center;
  margin-bottom: 60px;
}
.clients__items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 50px;
}
.clients__item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.clients__link {
  display: block;
}
.clients__link img {
  width: 100%;
}

.footer {
  padding: 80px 0;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/footer/footer.jpg") center/cover fixed no-repeat;
}
.footer__body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 30px;
  margin-bottom: 70px;
}
.footer__credit {
  text-align: center;
  font-size: 20px;
  color: #ffffff;
}
.footer__credit a {
  color: #fc2db7;
  font-weight: 700;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .footer__credit a:hover {
    color: #a7265d;
  }
}

.footer-column__title {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 25px;
}
.footer-column__item:not(.footer-column__item:last-child) {
  margin-bottom: 20px;
}
.footer-column__link {
  color: #ffffff;
  transition: color 0.3s ease 0s;
  display: inline-flex;
  align-items: center;
  line-height: 120%;
}
.footer-column__link::before {
  padding-right: 10px;
  transition: padding-right 0.3s ease 0s;
}
@media (any-hover: hover) {
  .footer-column__link:hover {
    color: #fc2db7;
  }
  .footer-column__link:hover::before {
    padding-right: 20px;
  }
}
.footer-column__form {
  margin-bottom: 25px;
}
.footer-column__label {
  display: block;
  min-height: 35px;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 20px;
}
.footer-column__input {
  display: block;
  min-height: 35px;
  width: 100%;
  padding: 0 10px;
  border: 2px solid #ffffff;
  color: #ffffff;
  font-size: 14px;
  background: none;
  outline: none;
  border-radius: 20px;
}
.footer-column__input::placeholder {
  color: #ffffff;
}
.footer-column__input:focus {
  border-color: #fc2db7;
}
.footer-column__btn {
  height: 40px;
  padding: 5px 25px;
  background-color: #fc2db7;
}

.footer-social {
  display: flex;
  align-items: center;
}
.footer-social__item {
  height: 45px;
  flex: 0 0 45px;
}
.footer-social__item:not(.footer-social__item:last-child) {
  margin-right: 5px;
}
.footer-social__link {
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 24px;
  color: #654f5e;
  background-color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}
@media (any-hover: hover) {
  .footer-social__link:hover {
    background-color: #fc2db7;
    color: #ffffff;
  }
}