*
	padding: 0
	margin: 0
	border: 0

*,
*::before,
*::after
	box-sizing: border-box

a:focus-visible,
button:focus-visible
	outline: 2px solid $pink-color
	outline-offset: 4px

aside,
nav,
footer,
header,
section
	display: block

html,
body
	height: 100%
	min-width: $min-width

body
	line-height: 1
	font-family: $font-family
	color: $main-color
	font-weight: 400
	font-style: normal

input,
button,
textarea
	font-size: inherit
	font-family: $font-family

button
	cursor: pointer
	background-color: transparent

a,
a:visited
	outline: none
	text-decoration: none

a:hover
	text-decoration: none

ul li
	list-style: none

img
	vertical-align: top

h1,
h2,
h3,
h4,
h5,
h6
	font-weight: inherit
	font-size: inherit

.wrapper
	width: 100%
	min-height: 100%
	overflow: hidden
	display: flex
	flex-direction: column

.main
	flex: 1 1 auto

	&__home
		padding: 200px 0 30px 0
		min-height: 600px

	&__benefits
		padding: 70px 0

	&__popular
		padding: 70px 0 50px 0

	&__partners
		padding: 70px 0

	&__new
		padding: 70px 0 50px 0

	&__categories
		padding: 70px 0

	&__clients
		padding: 70px 0

.container
	max-width: $max-width-container
	padding: 0 20px
	margin: 0 auto
	@media (max-width: $md3)
		padding: 0 10px

.btn
	display: inline-flex
	justify-content: center
	align-items: center
	color: $white-color
	background-color: $gray-color
	font-weight: 500
	border-radius: 20px
	transition: box-shadow 0.3s ease, background-color 0.3s ease
	@media (any-hover: hover)
		&:hover
			box-shadow: 0 0 3px 3px rgba(194, 63, 119, 0.4)
			background-color: $pink-color

.title
	position: relative
	// display: inline-block	
	font-weight: 700
	font-size: 42px
	color: $gray-color
	span
		color: $pink-color
	&::after
		content: ''
		position: absolute
		width: 15%
		height: 10px
		bottom: -20px
		left: 50%
		transform: translateX(-50%)
		background-color: $pink-color

.product-item
	width: 180px
	background-color: $white-color
	border-radius: 10px
	box-shadow: 0 0 10px 5px rgba(194, 63, 119, 0.4)

	&__image
		width: 100%
		margin-bottom: 10px
		img
			width: 100%
			height: 100%
			object-fit: cover
			border-top-left-radius: 10px
			border-top-right-radius: 10px

	&__content
		padding: 10px
		color: $gray-color

	&__title
		display: block
		font-size: 18px
		font-weight: 700
		margin-bottom: 10px
		color: $gray-color
		transition: color 0.3s ease
		@media (any-hover: hover)
			&:hover
				color: $pink-color

	&__prices
		margin-bottom: 10px

	&__price
		margin-right: 10px
		font-weight: 700

		&_old
			font-weight: 500
			text-decoration: line-through

	&__bottom
		display: grid
		grid-template-columns: 1.5fr 1.5fr 3fr
		gap: 10px
		width: 100%

.item-bottom

	&__label
		display: block
		width: 100%
		border: 1px solid $gray-color

	&__input
		display: flex
		align-items: center
		justify-content: center
		text-align: center
		width: 100%
		height: 100%

	&__btns
		display: flex
		flex-direction: column
		justify-content: center

	&__btn
		border-radius: 10px

		&_plus
			margin-bottom: 5px

		&_minus

	&__button
		border-radius: 10px
		padding: 5px
