.home
    background-image: linear-gradient(120deg, #ebedee 0%, #ffdbea 100%)

    &__slider
        position: relative
        padding: 0 40px

.slider-home

    &__body
        height: 400px
        width: 100%

    &__wrapper

    &__slide
        width: 100%
        height: 100%
        position: relative

    &__arrows

    &__arrow
        position: absolute
        top: 40%
        width: 40px
        height: 40px
        border-radius: 100%
        z-index: 10
        background-color: $gray-color

        &_prev
            left: -10px

        &_next
            right: -10px

    &__dotts
        display: flex
        justify-content: center
        margin-top: 30px

.slide-home

    &__image
        width: 100%
        height: 100%
        img
            width: 100%
            height: 100%
            object-fit: cover

    &__content
        position: absolute
        bottom: 20px
        left: 20px
        width: 45%
        padding: 20px
        background-color: rgba(255, 255, 255, 0.8)

    &__title
        color: $gray-color
        font-size: 32px
        font-weight: 700
        text-transform: uppercase
        margin-bottom: 15px

    &__descr
        color: $gray-color
        font-weight: 500
        margin-bottom: 20px

    &__btn
        height: 40px
        padding: 5px 15px
