.categories

    &__container

    &__title
        text-align: center
        margin-bottom: 60px

    &__body
        display: grid
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr))
        gap: 30px

    &__item
        display: flex
        flex-direction: column
        align-items: center
        text-align: center
        border: 2px solid $gray-color
        border-radius: 10px
        padding: 20px

.item-categories

    &__image
        width: 100%
        padding: 30px 60px
        img
            width: 100%
            height: 100%
            object-fit: cover

    &__title
        flex: 1 1 auto
        font-size: 22px
        font-weight: 700
        color: $gray-color
        margin-bottom: 20px

    &__btn
        height: 45px
        width: 100%
        padding: 5px 15px
