.header
    position: fixed
    top: 0
    left: 0
    width: 100%
    z-index: 500
    transition: background-color 0.3s ease, box-shadow 0.3s ease
    &.active
        background-color: rgba(238, 238, 238, 0.95)
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4)
        .search-header__body
            box-shadow: 0 0 2px 2px rgba(101, 79, 94, 0.4)

    &__body
        min-height: 150px
        display: flex
        align-items: center

    &__logo
        display: block
        flex: 0 0 160px
        background-color: $pink-color
        border-radius: 10px
        border: 7px solid $white-color
        margin-right: 40px
        img
            width: 100%
            height: 100%
            object-fit: cover

    &__content
        flex: 1 1 auto

    &__top
        display: flex
        align-items: center
        justify-content: space-between
        padding: 10px 0

    &__main
        display: flex
        align-items: center
        justify-content: space-between
        padding: 5px 0

    &__bottom
        padding: 10px 0
        display: flex
        align-items: center
        justify-content: space-between

.top-header

    &__phones
        display: flex
        align-items: center

    &__phone
        display: flex
        align-items: center
        color: $gray-color
        font-size: 14px
        font-weight: 700
        transition: color 0.3s ease
        &:not(:last-child)
            margin-right: 25px
        &:before
            font-size: 16px
            margin-right: 5px
        @media (any-hover: hover)
            &:hover
                color: $pink-color

.search-header

    &__body
        display: flex
        align-items: center
        height: 40px
        width: 400px
        background-color: $white-color
        border-radius: 20px
        padding: 2px
        transition: box-shadow 0.3s ease
        &:focus-within
            box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.5)

    &__label
        padding: 2px 10px
        width: 100%
        height: 100%

    &__input
        width: 100%
        height: 100%
        font-size: 14px
        color: $gray-color
        outline: none
        padding-left: 5px
        transition: box-shadow 0.3s ease

    &__btn
        flex: 0 0 36px
        height: 36px
        padding: 5px
        font-size: 16px
        border-radius: 100%
        background-color: $gray-color

.bottom-header

    &__menu

    &__list
        display: flex
        align-items: center

    &__item
        &:not(:last-child)
            margin-right: 30px

    &__link
        color: $gray-color
        position: relative
        font-weight: 700
        &::before,
        &::after
            content: ''
            position: absolute
            bottom: -5px
            width: 0
            height: 2px
            opacity: 0
            background-color: $pink-color
            transition: all 0.3s ease 0s
        &::before
            left: 50%
        &::after
            right: 50%
        @media (any-hover: hover)
            &:hover
                color: $pink-color
                &::before,
                &::after
                    width: 50%
                    opacity: 1

    &__actions
        display: flex
        align-items: center

    &__login,
    &__registration,
    &__cart
        font-size: 14px
        margin-right: 20px
        font-weight: 700
        color: $gray-color
        transition: color 0.3s ease
        &:before
            margin-bottom: 5px
            font-size: 22px
        span
            display: inline-block
            font-weight: 700
        @media (any-hover: hover)
            &:hover
                color: $pink-color

    &__burger
        display: none

    &__callback
        height: 40px
        padding: 5px 15px
        font-size: 14px
        background-color: $pink-color
        &::before
            margin-right: 5px
            font-size: 26px
